import React, { useState, useEffect } from 'react';
import { Modal } from '@fluentui/react';
import { fetchSentence } from '../DataHandler';
import { PretaggedToken } from '../datatypes';

export interface IFullTextModalProps {
  itemId: string;
  isOpen: boolean;
  onDismiss: () => void;
}

function loadFullText(itemId: string) {
  console.log("Loading fulltext...");
  const sentenceId = itemId.split(":")[0];
  const [batchId, sentenceNoStr] = sentenceId.split("-");
  if (!batchId || !sentenceNoStr) {
    Promise.reject("Cannot parse itemId");
  };

  const sentenceNo = parseInt(sentenceNoStr);
  if (isNaN(sentenceNo)) {
    Promise.reject("No sentence number")
  }

  const window = 10;
  const sentPromises = Array(window * 2 + 1).fill(0).map((_, i) => {
    const newNo = sentenceNo + (i - window);
    if (newNo < 0) {
      return null;
    } else {
      return fetchSentence(`${batchId}-${newNo}`);
    }
  }).filter((x) => x != null);

  const fulltext = Promise.allSettled(sentPromises)
    .then((results) => {
      return results.reduce((sents, x) => {
        if (x.status == "fulfilled" && x.value) {

          const tokens: PretaggedToken[] = (x.value as unknown) as PretaggedToken[];
          tokens.forEach((x) => sents.push(x));
        }
        return sents;
      }, [] as PretaggedToken[])
    });

  return fulltext;
}

export function FullTextModal(props: IFullTextModalProps) {

  const [fullText, setFullText] = useState([] as PretaggedToken[]);
  useEffect(() => {
    loadFullText(props.itemId).then((result) => {
      setFullText(result);
    }).catch((reason) => {
      console.error(reason);
    });

  }, [props.itemId]);

  return (
    <Modal
      titleAriaId={`full-text-${props.itemId}`}
      isOpen={props.isOpen}
      onDismiss={props.onDismiss}
      styles={{main: {maxWidth: "60%"}}}>
      <div style={{ fontSize: "large", padding: "5%", lineHeight: "150%" }}>
        {fullText.map((x) => {
          return (<span>{x[0]}</span>)
        })}
      </div>

    </Modal>

  );
}