import React, { useState, useEffect, useRef } from 'react';
import { SenseRevision, CwnSense, SenseTree } from '../datatypes';
import { Stack, IconButton, Announced, Rating, Dropdown, TextField, ITextField, DefaultPalette } from '@fluentui/react';
import { SensePicker } from './SensePicker';
import { parseFilter, cloneSenseRevision } from './sorter_utils';
import { SessionDispatch, SessionActionType } from '../SenseAnnot/sessionUtils/sessionReducer';
import { annot_exclusion_options } from './sense_sorter_options';

export interface IRevisionViewProps {
  revision: SenseRevision | null;
  senses: CwnSense[],
  senseTree: SenseTree | null,
  sessionDispatch: SessionDispatch;
  onSenseRevisionUpdated: (revision: SenseRevision) => void;
}

function getRating(revision: SenseRevision | null) {
  if (revision) {
    return revision.probs.length > 0 ?
      Math.floor(revision.probs[0] / 0.25) : 4;
  } else {
    return 4;
  }
}

export function RevisionView(props: IRevisionViewProps) {
  let revision = props.revision;
  let iconStyle = { height: "30px", width: "30px", fontSize: "100px" };
  
  const [isExcluded, setIsExcluded] = useState(props.revision?.status == "EXCLUDED");
  const suppTextRef = useRef<ITextField>(null);

  useEffect(() => {      
    setIsExcluded(props.revision?.status == "EXCLUDED");
  }, [props.revision]);

  if (revision) {

    const isRevisionComplete = (revision: SenseRevision) => {
      const status = revision.status;
      if (status && status == "EXCLUDED") {
        return true;
      } else {
        return revision.probs.length == 1;
      }
    }

    const onFilterSenses = (filter: string) => {
      return parseFilter(filter, props.senses, props.senseTree);
    }

    const onRevisionUpdated = (newRevision: SenseRevision) => {
      props.onSenseRevisionUpdated(newRevision);
    }

    const onSenseCandidatesUpdated = (newCandidates: string[]) => {
      if (revision) {
        const newRevision = cloneSenseRevision(revision, newCandidates);
        newRevision.status = "NORMAL";        
        // console.log("onRevisionUpdated", newRevision);
        onRevisionUpdated(newRevision);
      }
    }

    const onExclusionUpdated = (supp_text: string) => {
      if (revision) {
        const newRevision = cloneSenseRevision(revision);
        newRevision.status = "EXCLUDED";
        newRevision.note = supp_text;
        onRevisionUpdated(newRevision);
        // console.log("onExclusionUpdated", newRevision);
      }
    }

    const toggleIsExcluded = () => {
      if (revision) {
        const newRevision = cloneSenseRevision(revision);
        newRevision.status = newRevision.status == "EXCLUDED"? 
          "NORMAL": "EXCLUDED";
        newRevision.note = "";
        onRevisionUpdated(newRevision);        
      }
    };

    const selectedSenses = revision.candidates.map((x) => {
      let targets = props.senses.filter((sense) => sense.cwn_id == x);
      return (targets && targets.length > 0) ? targets[0] : null;
    }).filter((x) => x !== null) as CwnSense[];

    const [exclOptionKey, exclSuppText] = revision.note ?
      revision.note.split(";") : [undefined, ""];

    const exclusionElem = (
      <Stack horizontal styles={{ root: { width: "100%" } }}>
        <Stack.Item grow={1}>
          <Dropdown placeholder="()" label=""
            selectedKey={exclOptionKey}
            options={annot_exclusion_options}
            onChange={(ev, item) => {
              const itemKey = item ? item.key : "";
              onExclusionUpdated(`${itemKey};${exclSuppText}`)
            }} />
        </Stack.Item>
        <Stack.Item grow={2}>
          <TextField label="" componentRef={suppTextRef}
            defaultValue={exclSuppText}
            onBlur={(ev) => {
              const suppText = suppTextRef.current ? suppTextRef.current.value : "";
              onExclusionUpdated(`${exclOptionKey};${suppText}`)
              console.log("suppText onBlur");
            }}
          />
        </Stack.Item>
      </Stack>);

    const pickerElem = (<SensePicker
      senseCandidates={selectedSenses}
      onPickerChanged={onSenseCandidatesUpdated}
      onSenseSuggestions={onFilterSenses}
      onSubmit={() => { props.sessionDispatch(SessionActionType.NextTodo); }}
    />);

    return (
      <Stack horizontal verticalAlign="end"
        styles={{ root: { width: "100%;", paddingBottom: "1%" } }}>
        <Stack.Item grow={1}>
          {isExcluded ? exclusionElem : pickerElem}
        </Stack.Item>
        <Stack.Item styles={{ root: { width: "100px" } }}>
          <Stack tokens={{ childrenGap: 2 }} horizontal
            horizontalAlign="end" verticalAlign="end">
            <IconButton style={iconStyle}
              iconProps={{ iconName: 'CheckMark' }}
              title="" ariaLabel="CheckMark"
              disabled={!isRevisionComplete(revision)}
              onClick={() => { props.sessionDispatch(SessionActionType.NextTodo); }}
            />
            <IconButton style={iconStyle}
              iconProps={{ iconName: 'Communications' }}              
              title="" ariaLabel="Communications"
              styles={{root: {color: isExcluded? "red": DefaultPalette.themePrimary}}}
              onClick={toggleIsExcluded} />
          </Stack>
        </Stack.Item>
      </Stack>
    )
  } else {
    return null;
  }
}