import React, { useState } from 'react';
import { PretaggedToken } from '../datatypes';
import { Token } from './Token';
import { Stack, Link } from '@fluentui/react';
import { FullTextModal } from './FullTextModal';

export interface ISentenceViewerData {
  preContext?: PretaggedToken[]
  sentence: PretaggedToken[];
  postContext?: PretaggedToken[]
  itemId: string;
}

function SentenceViewer(props: ISentenceViewerData) {
  let sentence = props.sentence;
  let itemId = props.itemId;

  const [isFullTextVisible, setFullTextVisible] = useState(false);
    
  if (sentence === undefined) {
    return null;
  } else {
    let tokenId = -1;
    try {
      tokenId = parseInt(itemId.split(":")[1]);
    } catch (err) {
      //pass
    }

    const toggleFullTextVisible = () => {
      setFullTextVisible(!isFullTextVisible);
    }

    const preContextElems = props.preContext ?
      props.preContext.map((token, index) => {
        return (<Token token={token} simplified></Token>)
      }) : null;

    const postContextElems = props.postContext ?
      props.postContext.map((token, index) => {
        return (<Token token={token} simplified></Token>)
      }) : null;

    return (      
      <div className="row">
        <div className="mx-auto mt-4">
          <div className="position-relative card-body bg-light 
            p-3 mb-3 border border-primary rounded-lg">                   
            {isFullTextVisible? (
              <FullTextModal
                itemId={props.itemId}
                isOpen={isFullTextVisible}
                onDismiss={toggleFullTextVisible}/>
            ): null}
            <Link className="position-absolute" 
              style={{top: "1%", right: "1%", fontSize: "small"}}  
              onClick={toggleFullTextVisible}            
              ></Link>
            <Stack horizontal horizontalAlign="start" tokens={{ childrenGap: 3 }}
              styles={{root: {padding: "12pt 0", overflowX: "auto"}}}>
              {preContextElems}
              {sentence.map((token, index) => {
                let isFocus = tokenId == index;
                return (<Token token={token} isFocus={isFocus}></Token>)
              })}
              {postContextElems}
            </Stack>
          </div>
        </div>
      </div>
    )
  }
}

export default SentenceViewer;