
import React from 'react';
import { Dialog, DialogFooter, PrimaryButton, DefaultButton, DialogType, DefaultPalette } from '@fluentui/react';

export interface IResetSenseTreeDialogProps {
  hideDialog: boolean;
  onDismiss: () => void;
  onConfirmReset: () => void;
}

export function ResetSenseTreeDialog(props: IResetSenseTreeDialogProps) {
  const dialogContentProps = {
    type: DialogType.normal,
    title: '',
    closeButtonAriaLabel: 'Close',
    subText: '',
  };

  return (
    <Dialog
      hidden={props.hideDialog}
      onDismiss={props.onDismiss}
      dialogContentProps={dialogContentProps}

    >
      <DialogFooter>
        <PrimaryButton onClick={props.onConfirmReset} 
          styles={{root: {backgroundColor: DefaultPalette.red}}} text="" />
        <DefaultButton onClick={props.onDismiss} text="" />
      </DialogFooter>
    </Dialog>
  )
}